"use client";
import { useState } from "react";

// Tela simulada de cada seção
const Screen = ({ type, user }) => {
  switch (type) {
    case "chat":
      return null; // Chat principal já é renderizado fora
    case "config":
      return <div style={{ padding: "20px" }}><h2>Configurações</h2><p>Aqui você pode alterar tema, fonte, etc.</p></div>;
    case "profile":
      return (
        <div style={{ padding: "20px" }}>
          <h2>Perfil</h2>
          <img src={user?.photo || "https://via.placeholder.com/80"} alt="perfil" style={{ borderRadius: "50%" }} />
          <p><strong>{user?.name}</strong></p>
          <p>Nametag: {user?.nametag || "Nenhuma"}</p>
        </div>
      );
    case "voice":
      return <div style={{ padding: "20px" }}><h2>Chat de Voz</h2><p>Funcionalidade de chat de voz em desenvolvimento...</p></div>;
    default:
      return null;
  }
};

export default function ChatApp() {
  const [users, setUsers] = useState([
    { id: 1, name: "Alice", email: "alice@email.com", photo: "", nametag: "Ally" },
    { id: 2, name: "Bob", email: "bob@email.com", photo: "", nametag: "Bobby" },
  ]);

  const [loggedUser, setLoggedUser] = useState({ name: "Kaue Stephanini", photo: "", nametag: "Kaue#01" });

  const [selectedUserId, setSelectedUserId] = useState(null);
  const [messages, setMessages] = useState({ 1: [], 2: [] });
  const [newMessage, setNewMessage] = useState("");
  const [showAddUser, setShowAddUser] = useState(false);
  const [newUserName, setNewUserName] = useState("");
  const [newUserEmail, setNewUserEmail] = useState("");
  const [currentScreen, setCurrentScreen] = useState("chat"); // chat, config, profile, voice

  const handleSelectUser = (id) => {
    setSelectedUserId(id);
    setCurrentScreen("chat");
  };

  const handleSendMessage = (text, fromMe = true) => {
    if (!text || selectedUserId === null) return;
    setMessages((prev) => ({
      ...prev,
      [selectedUserId]: [...(prev[selectedUserId] || []), { text, fromMe }],
    }));
    setNewMessage("");
  };

  const handleAddUser = () => {
    if (!newUserName || !newUserEmail) return;
    const newUser = { id: Date.now(), name: newUserName, email: newUserEmail, photo: "", nametag: newUserName };
    setUsers((prev) => [...prev, newUser]);
    setMessages((prev) => ({ ...prev, [newUser.id]: [] }));
    setNewUserName("");
    setNewUserEmail("");
    setShowAddUser(false);
  };

  const selectedUserMessages = selectedUserId ? messages[selectedUserId] : [];

  return (
    <div style={{ display: "flex", height: "100vh", fontFamily: "Arial, sans-serif" }}>
      {/* Sidebar */}
      <div style={{ width: "250px", borderRight: "1px solid #ccc", padding: "10px", display: "flex", flexDirection: "column", justifyContent: "space-between" }}>
        <div>
          {/* Topo da lista com conta logada */}
          <div style={{ display: "flex", alignItems: "center", marginBottom: "15px" }}>
            <img src={loggedUser.photo || "https://via.placeholder.com/50"} alt="perfil" style={{ borderRadius: "50%", marginRight: "10px" }} />
            <div>
              <strong>{loggedUser.name}</strong>
              <p style={{ margin: 0, fontSize: "12px" }}>{loggedUser.nametag}</p>
            </div>
          </div>

          <h3>Usuários</h3>
          <ul style={{ listStyle: "none", padding: 0 }}>
            {users.map((user) => (
              <li
                key={user.id}
                onClick={() => handleSelectUser(user.id)}
                style={{
                  padding: "8px",
                  cursor: "pointer",
                  borderRadius: "8px",
                  background: selectedUserId === user.id ? "#eee" : "transparent",
                  marginBottom: "5px",
                }}
              >
                {user.name} ({user.email})
              </li>
            ))}
          </ul>
          <button style={{ borderRadius: "8px", padding: "8px", marginTop: "10px" }} onClick={() => setShowAddUser(true)}>Adicionar usuário</button>
        </div>

        {/* Botões inferiores */}
        <div style={{ display: "flex", flexDirection: "column", gap: "5px", marginTop: "10px" }}>
          <button style={{ borderRadius: "8px", padding: "8px" }} onClick={() => alert("Saindo da conta...")}>Sair da conta</button>
          <button style={{ borderRadius: "8px", padding: "8px" }} onClick={() => setCurrentScreen("config")}>Configurações</button>
          <button style={{ borderRadius: "8px", padding: "8px" }} onClick={() => setCurrentScreen("profile")}>Perfil</button>
          <button style={{ borderRadius: "8px", padding: "8px" }} onClick={() => setCurrentScreen("voice")}>Chat de voz</button>
        </div>
      </div>

      {/* Área principal */}
      <div style={{ flex: 1, display: "flex", flexDirection: "column" }}>
        {currentScreen === "chat" && (
          <>
            <div style={{ flex: 1, padding: "10px", overflowY: "auto", borderBottom: "1px solid #ccc" }}>
              {selectedUserId ? (
                selectedUserMessages.map((msg, idx) => (
                  <div
                    key={idx}
                    style={{
                      textAlign: msg.fromMe ? "right" : "left",
                      margin: "5px 0",
                      padding: "5px 10px",
                      background: msg.fromMe ? "#DCF8C6" : "#FFF",
                      display: "inline-block",
                      borderRadius: "10px",
                    }}
                  >
                    {msg.text}
                  </div>
                ))
              ) : (
                <p>Selecione um usuário para iniciar o chat.</p>
              )}
            </div>

            {selectedUserId && (
              <div style={{ display: "flex", padding: "10px" }}>
                <input
                  style={{ flex: 1, padding: "8px" }}
                  value={newMessage}
                  onChange={(e) => setNewMessage(e.target.value)}
                  onKeyDown={(e) => e.key === "Enter" && handleSendMessage(newMessage)}
                  placeholder="Digite sua mensagem..."
                />
                <button style={{ marginLeft: "5px", borderRadius: "8px", padding: "8px" }} onClick={() => handleSendMessage(newMessage)}>Enviar</button>
              </div>
            )}
          </>
        )}

        {currentScreen !== "chat" && <Screen type={currentScreen} user={loggedUser} />}
      </div>

      {/* Modal de adicionar usuário */}
      {showAddUser && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            width: "100vw",
            height: "100vh",
            background: "rgba(0,0,0,0.5)",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <div style={{ background: "#fff", padding: "20px", borderRadius: "8px", width: "300px" }}>
            <h3>Adicionar Usuário</h3>
            <input
              style={{ width: "100%", marginBottom: "10px", padding: "5px" }}
              placeholder="Nome"
              value={newUserName}
              onChange={(e) => setNewUserName(e.target.value)}
            />
            <input
              style={{ width: "100%", marginBottom: "10px", padding: "5px" }}
              placeholder="Email"
              value={newUserEmail}
              onChange={(e) => setNewUserEmail(e.target.value)}
            />
            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <button style={{ borderRadius: "8px", padding: "8px" }} onClick={handleAddUser}>Adicionar</button>
              <button style={{ borderRadius: "8px", padding: "8px" }} onClick={() => setShowAddUser(false)}>Cancelar</button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
